#skills {
  background-color: rgb(31, 31, 56);
  padding: 50px 0;
  text-align: center;
}

#skills h5 {
  color: #dad4d4;
  font-size: 18px;
  font-weight: 500;
  letter-spacing: 1px;
  margin-bottom: 5px;
  margin-top: 1rem;
}

#skills h2 {
  color: #41e873;
  font-size: 30px;
  font-weight: 600;
  margin-bottom: 30px;
}
.experience__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  padding: 1rem;
  /* border: 1px solid red; */
  margin: auto;
  padding: 5rem;
}
.experience__container > div {
  background: rgb(61, 61, 102);
  padding: 2.4rem 5rem;
  border-radius: 2rem;
  border: 5px solid transparent;
}
.experience__container > div:hover {
  background: transparent;
  border-color: rgba(130, 66, 66, 0.4);
  cursor: default;
}
.experience__container > div h3 {
  text-align: center;
  margin-bottom: 2rem;
  color: #41e873;
  font-size: 30px;
}
.experience__content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 2rem;
  /* border: 1px solid red; */
}
.skills-card {
  display: flex;
  gap: 1rem;
  /* border: 1px solid red; */
  text-align: center;
  font-size: 30px;
  color: white;
}
.skills-card-img {
  margin-top: 6px;
  font-size: 40px;
  color: #41e873;
}
/* ================ MEDIA QUERIES (MEDIUM DEVICES) ============= */

@media screen and (max-width: 1024px) {
  .experience__container {
    grid-template-columns: 1fr;
  }
  .experience__container > div {
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
  }
  .experience__content {
    padding: 1rem;
  }
}
/* ================ MEDIA QUERIES (SMALL DEVICES) ============= */
@media screen and (max-width: 600px) {
  .experience__container {
    gap: 1rem;
    
  }
  .experience__container > div {
    width: 100%;
    padding: 2rem 1rem;
  }
}
